import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { IRangeExpressApiSetupService } from '../@intermediate/range-express-api-setup.interface';
import { GetRangeExpressApiSetupRequest, GetRangeExpressApiSetupResponse } from '../@intermediate/models/get-range-express-api-setup.model';
import { PostRangeExpressApiSetupRequest, PostRangeExpressApiSetupResponse } from '../@intermediate/models/post-range-express-api-setup.model';

@Injectable()
export class RangeExpressApiSetupService implements IRangeExpressApiSetupService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/DeviceIntegration/RangeServantIntegration';
  }
  Get(request: GetRangeExpressApiSetupRequest): Observable<GetRangeExpressApiSetupResponse>
  {
    const apiUrl = this.serviceUrl + '/GetRangeExpressApiSetup';
    return this.http.get<GetRangeExpressApiSetupResponse>(apiUrl, { params: request as any }
    );
  }
  Post(request: PostRangeExpressApiSetupRequest): Observable<PostRangeExpressApiSetupResponse>
  {
    const apiUrl = this.serviceUrl + '/PostRangeExpressApiSetup';
    return this.http.post<PostRangeExpressApiSetupResponse>(apiUrl, request);
  }
}