import { Component, Inject } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { ManageLoadingService } from '@tei/core';
import { BreadCrumb, SnackBarService } from '@tei/material';
import { firstValueFrom, Subscription } from 'rxjs';
import { IHostAdminCenterHelperService } from '../../../@intermediate/host-admin-center-helper.interface';
import { GetRangeExpressApiSetupResponse, RangeExpressApiSetupForm } from '../../../@intermediate/models/get-range-express-api-setup.model';
import { portalGroupDetails } from '../../../@intermediate/models/host-admin-center-helper.model';
import { PostRangeExpressApiSetupRequest } from '../../../@intermediate/models/post-range-express-api-setup.model';
import { IRangeExpressApiSetupService } from '../../../@intermediate/range-express-api-setup.interface';

@Component({
  selector: 'tei-admin-admin-center-range-express-api-setup',
  templateUrl: './range-express-api-setup.component.html',
  styleUrls: ['./range-express-api-setup.component.css']
})
export class RangeExpressApiSetupComponet
{
  setupID?: number;
  breadCrumbData: BreadCrumb[] = [];
  subscription: Subscription = new Subscription();

  portalDetail: portalGroupDetails;

  form = new FormGroup<RangeExpressApiSetupForm>({
    SetupID: new FormControl<number>(null),
    Site: new FormControl<string>('0', [Validators.required, Validators.pattern(/^\d+$/)]),
    Target: new FormControl<string>('', [Validators.required, Validators.pattern(/^(?:\d{1,3}\.){3}\d{1,3}$/)]),
    Port: new FormControl<string>('', [Validators.required, Validators.min(0), Validators.max(65535)]),
    ExpiryDays: new FormControl<number>(1, Validators.required),
    Uses: new FormControl<number>(1, Validators.required),
    IsEnabled: new FormControl<boolean>(false)
  });


  constructor(
    @Inject('IHostAdminCenterHelperService') private hostAdminCenterHelperService: IHostAdminCenterHelperService,
    @Inject("IRangeExpressApiSetupService") private rangeExpressApiSetupService: IRangeExpressApiSetupService,
    private snackBar: SnackBarService,
    private translate: TranslateService,
    public manageLoadingService: ManageLoadingService)
  {

  }

  ngOnInit(): void
  {
    this.subscription.add(this.hostAdminCenterHelperService.currentPortalGroupDetails.subscribe(portalDetail =>
    {
      if (portalDetail)
      {
        this.portalDetail = portalDetail;
        this.fillBreadCrump(portalDetail);
        this.loadData();
      }
    }));

  }

  async loadData(): Promise<void>
  {
    try
    {
      this.manageLoadingService.startLoading();

      const response = await firstValueFrom(this.rangeExpressApiSetupService.Get({ PortalID: this.portalDetail.portal.PortalID }));
      this.initForm(response);
    }
    catch (error)
    {
    }
    finally
    {
      this.manageLoadingService.finishLoading();
    }
  }

  initForm(response: GetRangeExpressApiSetupResponse): void
  {
    this.setupID = response?.SetupID;

    this.form.patchValue({
      Site: response?.Site,
      Target: response?.Target,
      Port: response?.Port,
      ExpiryDays: response?.ExpiryDays ?? 1,
      Uses: response?.Uses ?? 1,
      IsEnabled: response?.IsEnabled ?? false
    });
  }


  async saveSetting(): Promise<void>
  {
    try
    {
      this.manageLoadingService.startLoading();

      const request: PostRangeExpressApiSetupRequest = {
        ...this.form?.value as PostRangeExpressApiSetupRequest,
        SetupID: this.setupID,
        PortalID: this.portalDetail?.portal?.PortalID
      };

      var response = await firstValueFrom(this.rangeExpressApiSetupService.Post(request));

      this.setupID = response?.SetupID;


      this.snackBar.showSuccess(this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.RANGE_EXPRESS_SETUP.SAVED_SUCCESSFULLY'), '', null, 'left');
    }
    catch (error)
    {
    }
    finally
    {
      this.manageLoadingService.finishLoading();
    }
  }

  fillBreadCrump(portalDetail): void
  {
    this.breadCrumbData = [
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.PORTAL_GROUPS.TITLE'),
        relativeUrl: '../../../../../../',
      },
      {
        title: `${ portalDetail.parentPortalGroup.PortalGroup.PortalGroupName }`,
        relativeUrl: `../../../../../`,
      },
      {
        title: `${ portalDetail.portal.PortalName }`,
        relativeUrl: '../../../../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.ACCESS'),
        relativeUrl: '../../../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.INTEGRATION.TITLE'),
        relativeUrl: '../../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.RANGEMACHINEINTEGRATION'),
        relativeUrl: '../',
      },
      {
        title: this.translate.instant('ADMIN_CENTER.GROUP.MANAGE_PORTAL_GROUP.RANGE_EXPRESS_SETUP.TITLE'),
        relativeUrl: '',
      }
    ];
  }

  ngOnDestroy(): void 
  {
    this.subscription.unsubscribe();
  }
}
